import React from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import { Grid, Box, makeStyles, Typography } from "@material-ui/core";
import { path_CLIENT } from "../../configs";
import {
  varItem,
  varImg,
  varZoomInOut,
  varTransition,
  UrlFormat,
  ScrollMagicFadeIn,
} from "../../utilities";
import { Spinners } from "../../commons";

function ProjectItem(props) {
  const classes = useStyles();
  const { item } = props;
  const itemName = UrlFormat(item.name);
  const itemThumb = item.thumbnail.map((item) => item.url);

  return (
    <Grid item xs={12} sm={6}>
      <ScrollMagicFadeIn triggerHook={0.64} offset={80} duration={560}>
        <motion.div variants={varZoomInOut}>
          <Box
            to={path_CLIENT.projects.root + itemName}
            color="inherit"
            component={Link}
            style={{ textDecoration: "none" }}
          >
            <Box className={classes.root}>
              <motion.div
                whileTap="tap"
                whileHover="hover"
                variants={varItem}
                transition={varTransition}
              >
                <Box className={classes.thumbnail}>
                  <motion.img
                    src={itemThumb}
                    alt={itemThumb}
                    variants={varImg}
                    transition={varTransition}
                    className={classes.img}
                  />
                  <Spinners index={1} />
                </Box>
              </motion.div>

              <Box className={classes.caption}>
                <Typography variant="caption" color="textSecondary">
                  {item.category}
                </Typography>
                <Typography variant="subtitle1">{item.name}</Typography>
              </Box>
            </Box>
          </Box>
        </motion.div>
      </ScrollMagicFadeIn>
    </Grid>
  );
}
export default ProjectItem;

const useStyles = makeStyles((theme) => ({
  root: {
    overflow: "hidden",
    boxShadow: theme.shadows[25].card.root,
    borderRadius: theme.shape.borderRadiusLg,
    backgroundColor: theme.palette.background.card,
    transition: `all  ${theme.transitions.duration.shorter}ms ${theme.transitions.easing.easeIn}`,
    "&:hover": {
      boxShadow: theme.shadows[25].card.hover,
    },
  },
  thumbnail: {
    overflow: "hidden",
    paddingTop: "136%",
    position: "relative",
    borderRadius: theme.shape.borderRadiusLg,
    border: `${theme.palette.background.card} solid 6px`,
  },
  img: {
    top: 0,
    zIndex: 2,
    width: "100%",
    height: "100%",
    objectFit: "cover",
    position: "absolute",
    borderRadius: theme.shape.borderRadiusLg,
  },
  caption: {
    padding: theme.spacing(3),
    "&:hover": {
      boxShadow: theme.shadows[25].card.root,
    },
  },
}));
